  cursor: pointer;
  background-color: #444;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 1rem;
  transition: background-color .3s, color .3s;
}

.Darkmode:hover {
  color: #fff;
  background-color: #e67e22;
}

.Darkmode:focus {
  outline: none;
  box-shadow: 0 0 0 2px #e67e2280;
}

.search-bar-container {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
  display: flex;
}

.search-input {
  border: 1px solid #ccc;
  border-radius: 6px;
  outline: none;
  padding: 10px 16px;
  font-size: 1rem;
  transition: border .2s;
}

.search-input:focus {
  border: 1.5px solid #e67e22;
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background: #e67e22;
  border: none;
  border-radius: 6px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background .2s;
}

.search-btn:hover {
  background: #ca6f1e;
}

.main-card-div.light {
  color: #000;
  background-color: #fff;
}

.main-card-div.dark {
  color: #f1f1f1;
  background-color: #121212;
}

